/* 使用less语法需要加lang='less' 每一行后面必须加;代表结束 */
/* 移动端flex布局。尽量不适用float,用inline-block */
#app .tab[data-v-59536e82] {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  /* 变量以@开始,必须初始化一个值如  @color: red */
  position: relative;
  /* .border-1px会报undefined,也许是不支持数字 */
}
#app .tab[data-v-59536e82]:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: '';
  width: 100%;
}
#app .tab .tab-item[data-v-59536e82] {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
#app .tab .tab-item a[data-v-59536e82] {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .tab-item.active a[data-v-59536e82] {
  color: #f01414;
}



